.sending-field {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 10px;
    margin-right: 10px;
    margin-left: 9px;
    margin-bottom: 10px;
}

.sending-button {
    position: absolute;
    right: 39px;
    background: url('../../resources/img/Arrow.svg') center center / cover no-repeat;
    width: 20px;
    height: 20px;
    border: none;
}

.text-message {
    height: 74px;
    border-radius: 10px;
    width: 100%;
    background-color: rgb(71,70,68);
    color: white;
}