ul {
    position: absolute;
    right: 35px;
}

li {
    display: flex;
    margin-top: 17px;
    margin-right: 0;
    list-style: none;
    height: 50px;
    background-color: rgb(71,70,68);
    border-radius: 15px;
    color: white;
    align-items: center;
    width: fit-content;
}

li::after {
    content: "";
    width: 20px;
}

li::before {
    content: '';
    width: 14px;
}